      <ul id="checkbox">
        <li>
          <input
            type="checkbox"
            id="checkbox-main"
            v-model="select_main"
            @change="targetselect"
          />
          <label for="checkbox-main" class="label"><p>EDF6 MAINSTORY</p></label>
        </li>
        <li>
          <input
            type="checkbox"
            id="checkbox-dlc1"
            v-model="select_dlc1"
            @change="targetselect"
          />
          <label for="checkbox-dlc1" class="label">
            <p>LOST DAYS</p>
          </label>
        </li>
        <li>
          <input
            type="checkbox"
            id="checkbox-dlc2"
            v-model="select_dlc2"
            @change="targetselect"
          />
          <label for="checkbox-dlc2" class="label">
            <p>VISIONS OF MALICE</p>
          </label>
        </li>
      </ul>
